import React, { Component } from 'react';
import Card from './contactCard.js';
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Image from 'react-bootstrap/Image'

class Contact extends Component{

	render(){

		/*Follow on:

		1. Github
		2. Facebook
		3. Instagram
		4. Hackerrank
		5. Hackerearth
		6. Leetcode
		*/

		return(

			<Container id={this.props.id} >
				<h1 className="h1 my-5 text-center text-uppercase font-weight-bold text-black-50">Follow me</h1>
				<Row>
					{this.props.socialLinks.map((socialLink) => {

						return(
							<Col>
								<a href={socialLink.link} title={socialLink.platform} target="_blank">
									<Image src={socialLink.icon} width={64} height={64} alt={socialLink.platform} />
								</a>
							</Col>
						);

					})}

				</Row>

			</Container>

		);

	}

}

export default Contact;